import React, { useEffect } from "react";
import QuantityControl from "../QuantityControl";
import { useSelector, useDispatch } from "react-redux";
import * as styles from "./styles.module.css";
import * as otherstyles from "../styles.module.css";
import {
    toggleItemInCart,
    updateItemQuantity,
    selectCart,
    getMerchantMode,
} from "../../../state/merchantSlice";

const ItemModal = ({ item, onClose }) => {

    const dispatch = useDispatch();
    const cart = useSelector(selectCart);
    const merchantMode = useSelector(getMerchantMode);
    const isInCart = (id) => cart.some((cartItem) => cartItem.id === id);
    const quantity = isInCart(item.id) ? cart.find((cartItem) => cartItem.id === item.id).quantity : 1;

    const handleToggle = (event) => {
        event.stopPropagation(); // Prevent triggering the item click
        dispatch(toggleItemInCart(item));
    };

    const handleQuantityChange = (quantity) => {
        dispatch(updateItemQuantity({ id: item.id, quantity }));
    };

    useEffect(() => {
        const handleKeyDown = (event) => {
            if (event.key === "Escape") {
                onClose();
            }
        };

        document.addEventListener("keydown", handleKeyDown);
        return () => {
            document.removeEventListener("keydown", handleKeyDown);
        };
    }, [onClose]);

    return (
        <div className={styles.modalOverlay} onClick={onClose}>
            <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
                <button className={styles.closeButton} onClick={onClose}>âœ•</button>
                <h2>{item.name}</h2>
                <img src={item.image} alt={item.name} className={styles.modalImage} />
                <p>{item.description}</p>
                <p>${item.price}</p>
                {merchantMode ? (
                    <div>
                        <p># Remaining: {item.stock}</p>
                        {isInCart(item.id) ? (
                            <button onClick={handleToggle} className={otherstyles.removeButton}>
                                Remove from Cart
                            </button>
                        ) : (
                            <button onClick={handleToggle} disabled={item.stock <= 0}>
                                Add to Cart
                            </button>
                        )}
                        {isInCart(item.id) && (
                            <QuantityControl
                                quantity={quantity}
                                stock={item.stock}
                                onDecrease={() => handleQuantityChange(Math.max(1, quantity - 1))}
                                onIncrease={() => handleQuantityChange(Math.min(item.stock, quantity + 1))}
                            />
                        )}
                    </div>
                ) : null}
            </div>
        </div>
    );
};

export default ItemModal;
