import * as styles from "./styles.module.css";
import * as otherstyles from "../../styles/index.module.css";
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    selectCart,
    updateItemQuantity,
    toggleItemInCart,
    getMerchantMode,
} from '../../state/merchantSlice';
import PayPalButtons from "./checkout";
import QuantityControl from "./QuantityControl";

const ViewCart = () => {
    const dispatch = useDispatch();
    const cart = useSelector(selectCart);
    const merchantMode = useSelector(getMerchantMode);

    const handleQuantityChange = (item, quantity) => {
        const newQuantity = Math.max(1, Math.min(quantity, item.stock)); // Limit between 1 and stock
        dispatch(updateItemQuantity({ id: item.id, quantity: newQuantity }));
    };

    const handleRemoveItem = (item) => {
        dispatch(toggleItemInCart(item)); // Use toggle to remove item
    };

    if (merchantMode && (!cart || cart.length === 0)) {
        return (
            <div className={styles.emptyCartMessage}>
                <p>Your cart is empty</p>
            </div>
        );
    }

    if (!merchantMode) return null

    return (
        <div className={styles.cart}>
            <br />
            <table className={styles.cartTable}>
                <thead>
                    <tr>
                        <th>Item</th>
                        <th className={styles.quant}>Quantity</th>
                        <th className={styles.tot}>Total</th>
                        <th className={styles.del}>X</th>
                    </tr>
                </thead>
                <tbody>
                    {cart.map((item, index) => {
                        const price = Number(item.price) || 0;
                        const quantity = Number(item.quantity) || 0;
                        const total = price * quantity;

                        return (
                            <tr key={index}>
                                <td className={otherstyles.center}>
                                    {item.name || "Unnamed Item"}<br />
                                    <span className={styles.priceEach}>${price.toFixed(2)}</span>
                                </td>
                                <td>
                                    <QuantityControl
                                        quantity={quantity}
                                        stock={item.stock}
                                        onDecrease={() => handleQuantityChange(item, Math.max(1, quantity - 1))}
                                        onIncrease={() => handleQuantityChange(item, Math.min(item.stock, quantity + 1))}
                                        />
                                </td>
                                <td className={styles.tot}>${total.toFixed(2)}</td>
                                <td>
                                    <button
                                        onClick={() => handleRemoveItem(item)}
                                        className={styles.removeButton}
                                    >
                                        X
                                    </button>
                                </td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
            <h3 className={styles.grandTotal}>
                Grand Total: $
                {cart.reduce(
                    (total, item) =>
                        total + (Number(item.price) || 0) * (Number(item.quantity) || 0),
                    0
                ).toFixed(2)}
            </h3>

            <PayPalButtons cart={cart} />
        </div>
    );
};

export default ViewCart;
