import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { toggleItemInCart, updateItemQuantity, selectCart } from "../../../state/merchantSlice";
import { navigate } from "gatsby";
import Category from "./Category";
import * as styles from "../styles.module.css";

const ItemList = () => {
    const dispatch = useDispatch();
    const cart = useSelector(selectCart);
    const storeItems = useSelector((state) => state.merchantShop.storeItems);

    // Group items by category and section
    const groupedItems = new Map();
    storeItems.forEach((item) => {
        if (!groupedItems.has(item.category)) {
            groupedItems.set(item.category, new Map());
        }
        const categorySections = groupedItems.get(item.category);
        if (!categorySections.has(item.section)) {
            categorySections.set(item.section, []);
        }
        categorySections.get(item.section).push(item);
    });

    const [openCategory, setOpenCategory] = useState(null);
    const [openSection, setOpenSection] = useState(null);

    const handleItemClick = (item) => {
        navigate(`/shop?item=${item.id}`);
    };

    return (
        <div className={styles.itemListContainer}>
            {Array.from(groupedItems.entries()).map(([category, categorySections]) => (
                <Category
                    key={category}
                    category={category}
                    categorySections={categorySections}
                    openCategory={openCategory}
                    setOpenCategory={setOpenCategory}
                    openSection={openSection}
                    setOpenSection={setOpenSection}
                    handleItemClick={handleItemClick}
                    cart={cart}
                    dispatch={dispatch}
                />
            ))}
        </div>
    );
};

export default ItemList;
