import React from "react";
import Item from "./Item";
import * as styles from "../styles.module.css";

const Section = ({
    section,
    items,
    category,
    openSection,
    setOpenSection,
    handleItemClick,
    cart,
    dispatch,
}) => {
    const toggleSection = () => {
        const newSection =
            openSection?.category === category && openSection?.section === section
                ? null
                : { category, section };
        setOpenSection(newSection);

        // Scroll the section header into view
        if (newSection) {
            setTimeout(() => {
                const sectionElement = document.getElementById(`section-${section}`);
                if (sectionElement) {
                    sectionElement.scrollIntoView({ behavior: "smooth", block: "start" });
                }
            }, 0);
        }
    };

    return (
        <div className={styles.section}>
            {section && (
                <h3
                    id={`section-${section}`}
                    className={styles.sectionHeader}
                    onClick={toggleSection}
                >
                    {section}
                </h3>
            )}
            {(openSection?.category === category && openSection?.section === section) || !section ? (
                <div className={styles.itemList}>
                    {items.map((item) => (
                        <Item
                            key={item.id}
                            item={item}
                            cart={cart}
                            dispatch={dispatch}
                            handleItemClick={handleItemClick}
                        />
                    ))}
                </div>
            ) : null}
        </div>
    );
};

export default Section;
