import React, { useState, useEffect } from "react";
import { useSelector } from 'react-redux';
import { Link } from "gatsby";
import * as styles from "./headerLink.module.css";

const ModularHeaderLink = ({ leftWord = "walkum", leftLinkTo="/", rightWord, rightLinkTo=".", cursorColor = "#FFD700" }) => {
    const [blinkSpeed, setBlinkSpeed] = useState(getRandomBlinkSpeed());
    const accountInfo = useSelector((state) => state.account.accountInfo);

    // Function to generate a random blink speed between 1s and 3s
    function getRandomBlinkSpeed() {
        return Math.random() * (3000 - 1000) + 1000; // Random value between 1000ms and 3000ms
    }

    useEffect(() => {
        const interval = setInterval(() => {
            setBlinkSpeed(getRandomBlinkSpeed()); // Update blink speed every 5 seconds
        }, 5000);

        return () => clearInterval(interval); // Cleanup on unmount
    }, []);

    if (!accountInfo) {
        return null;
    }

    return (
        <div style={{ display: "flex", justifyContent: "center" }}>
            <div className={styles.phraseContainer}>
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 1200 300"
                    width="100%"
                    height="auto"
                    className={styles.responsiveSvg}
                >
                    <g transform="translate(600, 150)" textAnchor="middle">
                        {/* Left Word */}
                        <Link to={leftLinkTo}>
                            <text
                                x="-300"
                                fill="white"
                                fontFamily="Courier"
                                fontSize="90px"
                                className={styles.svgButton}
                            >
                                {leftWord}
                            </text>
                        </Link>

                        {/* Blinking Cursor */}
                        <text
                            x="0"
                            fill={cursorColor}
                            fontFamily="Courier"
                            fontSize="90px"
                            fontWeight="bold"
                            className={styles.blinkingCursor}
                            style={{
                                animationDuration: `${blinkSpeed}ms`,
                                transform: "scale(5, 5)",
                            }}
                        >
                            |
                        </text>

                        {/* Right Word */}
                        <Link to={rightLinkTo}>
                            <text
                                x="300"
                                fill="white"
                                fontFamily="Courier"
                                fontSize="90px"
                                className={styles.svgButton}
                            >
                                {rightWord}
                            </text>
                        </Link>
                    </g>
                </svg>
            </div>
        </div>
    );
};

export default ModularHeaderLink;
