import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import StoreItems from './storeItems';
import ShoppingCart from './cart';
import Footer from '../footer';
import ModularHeaderLink from "../links/modularHeaderLink";
import { navigate } from "gatsby";
import ItemModal from "./storeItems/ItemModal";
import {
    toggleCartView,
    selectCartVisibility,
    updateStoreItems,
    cleanCart,
    selectCart,
    setMerchantMode,
} from '../../state/merchantSlice';
import * as styles from "./styles.module.css";
import Loading from "../loading";
import * as otherstyles from "../../styles/index.module.css";
import { useMenu } from "../../spreadsheet/useSheet";

const Merchant = ({
    merchantMode = true,
    merchantSheet = `products`,
}) => {
    const dispatch = useDispatch();
    const isCartVisible = useSelector(selectCartVisibility);
    const cart = useSelector(selectCart);
    const merchantID = useSelector((state) => state.account.sheetID)
    const accountInfo = useSelector((state) => state.account.accountInfo);

    const { loading, error, data: menuData } = useMenu({
        spreadsheetId: merchantID,
        sheetName: merchantSheet,
    });

    useEffect(() => {
        if (!loading && !error && menuData) {
            dispatch(updateStoreItems(menuData));
            dispatch(cleanCart()); // Validate cart after store items update
        }
    }, [loading, error, menuData, dispatch]);

    useEffect(() => {
        dispatch(setMerchantMode(merchantMode));
    }, [merchantMode, dispatch]);

    const handleToggleCart = () => {
        dispatch(toggleCartView());
    };

    const queryParams = new URLSearchParams(typeof window !== "undefined" ? window.location.search : "");
    const selectedItemID = queryParams.get("item");

    const storeItems = useSelector((state) => state.merchantShop.storeItems);
    const selectedItem = storeItems.find((item) => item.id === selectedItemID);

    const closeModal = () => {
        navigate("/shop"); // Reset URL to close modal
    };

    if (loading) return <Loading />;
    if (error) return <p>Error loading store items: {error}</p>;

    // Only render the cart button and toggle if there are items in the cart and if in merchantMode
    const showCartButton = cart && cart.length > 0 && merchantMode;

    return (
        <div>
            <div className={otherstyles.container}>
                {
                    merchantMode ? (
                        <ModularHeaderLink
                            leftWord={accountInfo.name}
                            // leftLinkTo="."
                            rightWord={accountInfo.merchTitle}
                            cursorColor="#39FF14"
                        />
                    ) : (
                        <ModularHeaderLink
                            rightWord="eats"
                            cursorColor="#39FF14"
                        />
                    )
                }
                {showCartButton && (
                    <button className={styles.cartToggleButton} onClick={handleToggleCart}>
                        {isCartVisible ? (
                            <div className={styles.cartToggleContainer}>
                                <span>Hide</span>
                                <span className={styles.cartIcon} role="img" aria-label="cart">{'\u{1F6D2}'}</span>
                            </div>
                        ) : (
                            <div className={styles.cartToggleContainer}>
                                <span>Your Cart</span>
                                <span className={styles.cartIcon} role="img" aria-label="cart">{'\u{1F6D2}'}</span>
                            </div>
                        )}
                    </button>
                )}
                {isCartVisible && <ShoppingCart />}
            </div>
            <div className={styles.itemListContainer}>
                <StoreItems />
            </div>
            <div className={otherstyles.container}>
                <Footer />
            </div>
            {selectedItem && <ItemModal item={selectedItem} onClose={closeModal} />}
        </div>
    );
};

export default Merchant;
