import React from "react";
import Section from "./Section";
import * as styles from "../styles.module.css";

const Category = ({
    category,
    categorySections,
    openCategory,
    setOpenCategory,
    openSection,
    setOpenSection,
    handleItemClick,
    cart,
    dispatch,
}) => {
    const toggleCategory = () => {
        const newCategory = openCategory === category ? null : category;
        setOpenCategory(newCategory);

        // Collapse all sections if the category is closed
        if (!newCategory) {
            setOpenSection(null);
        }

        // Scroll the category header into view
        if (newCategory) {
            setTimeout(() => {
                const categoryElement = document.getElementById(`category-${category}`);
                if (categoryElement) {
                    categoryElement.scrollIntoView({ behavior: "smooth", block: "start" });
                }
            }, 0);
        }
    };

    return (
        <div className={styles.category}>
            <h2
                id={`category-${category}`}
                className={styles.categoryHeader}
                onClick={toggleCategory}
            >
                {category}
            </h2>
            {openCategory === category && (
                <div className={styles.sectionList}>
                    {Array.from(categorySections.entries()).map(([section, items]) => (
                        <Section
                            key={section}
                            section={section}
                            items={items}
                            category={category}
                            openSection={openSection}
                            setOpenSection={setOpenSection}
                            handleItemClick={handleItemClick}
                            cart={cart}
                            dispatch={dispatch}
                        />
                    ))}
                </div>
            )}
        </div>
    );
};

export default Category;
