import React from "react";

const QuantityControl = ({ quantity, stock, onDecrease, onIncrease }) => (
    <div>
        <button
            onClick={(e) => {
                e.stopPropagation();
                onDecrease();
            }}

        >
            -
        </button>
        <span>   {quantity}   </span>
        <button
            onClick={(e) => {
                e.stopPropagation();
                onIncrease();
            }}
        >
            +
        </button>
    </div>
);

export default QuantityControl;
