import React, { useRef } from "react";
import { clearCart } from "../../../state/merchantSlice";
import { useDispatch, useSelector } from "react-redux";
import * as styles from "./index.module.css";
import usePaypalButtons from "./usePaypalButtons";

const PaypalButton = ({ cart }) => {
    const dispatch = useDispatch();
    const paypalRef = useRef(null);

    const serverIP = process.env.SERVER_IP;
    const port = process.env.PORT;

    const merchantEmail = useSelector((state) => state.account.accountInfo.email);

    const createOrder = async (data, actions) => {
        try {
            const totalAmount = cart.reduce(
                (sum, item) => sum + parseFloat(item.price) * item.quantity,
                0
            ).toFixed(2);

            const response = await fetch(`https://${serverIP}:${port}/api/orders`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Origin": window.location.origin, // Include Origin header
                },
                body: JSON.stringify({ cart }),
            });

            const orderData = await response.json();
            console.log("createOrder Response:", orderData);

            if (orderData.id) {
                return orderData.id;
            }
            throw new Error("Failed to create order");
        } catch (error) {
            console.error("createOrder Error:", error);
            throw new Error("Could not initiate PayPal Checkout");
        }
    };

    const onApprove = async (data, actions) => {
        try {
            const response = await fetch(
                `https://${serverIP}:${port}/api/orders/${data.orderID}/capture`,
                {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                        "Origin": window.location.origin, // Include Origin header
                    },
                    body: JSON.stringify({ cart, merchantEmail }),
                }
            );

            const orderData = await response.json();
            console.log("onApprove Response:", orderData);

            if (orderData?.purchase_units?.[0]?.payments?.captures?.[0]) {
                const transaction =
                    orderData?.purchase_units?.[0]?.payments?.captures?.[0];
                console.log("Transaction completed:", transaction);

                dispatch(clearCart());
            } else {
                throw new Error("Capture failed");
            }
        } catch (error) {
            console.error("onApprove Error:", error);
            alert("Sorry, your transaction could not be processed.");
        }
    };

    // Initialize PayPal buttons
    usePaypalButtons({ cart, paypalRef, createOrder, onApprove });

    return (
        <div ref={paypalRef}></div>
    );
};

export default PaypalButton;
