import { useEffect, useRef } from "react";

const usePaypalButtons = ({ cart, paypalRef, onApprove, createOrder }) => {
    const paypalRenderedRef = useRef(false);

    useEffect(() => {
        if (process.env.NODE_ENV === 'development') console.log("\nEnvironment Variables:", {
            PAYPAL_CLIENT_ID: process.env.PAYPAL_CLIENT_ID ? "***" : undefined,
            SERVER_IP: process.env.SERVER_IP,
            PORT: process.env.PORT,
        });

        if (paypalRenderedRef.current) {
            console.log("PayPal buttons already rendered. Skipping initialization.");
            return;
        }

        const script = document.createElement("script");

        // Check if the user is on a mobile device
        const isMobile = /Mobi|Android/i.test(navigator.userAgent);

        let paypalEnv = process.env.NODE_ENV === 'production' ? 'https://www.paypal.com' : 'https://www.sandbox.paypal.com';
        let buyerCountryParam = process.env.NODE_ENV !== 'production' ? '&buyer-country=US' : '';

        // Set script.src with shared parameters
        script.src = `${paypalEnv}/sdk/js?client-id=${encodeURIComponent(process.env.PAYPAL_CLIENT_ID)}${buyerCountryParam}&currency=USD&components=buttons&enable-funding=paypal,venmo,card&commit=true&intent=capture${isMobile ? '&integration-date=2021-02-01' : ''}`;

        script.async = true;

        script.onload = () => {
            if (window.paypal && paypalRef.current) {
                paypalRenderedRef.current = true;
                window.paypal.Buttons({
                    style: {
                        shape: "pill",
                        layout: "vertical",
                        color: "blue",
                        label: "pay",
                    },
                    createOrder,
                    onApprove,
                }).render(paypalRef.current);
            } else {
                console.error("PayPal SDK not loaded properly.");
            }
        };

        document.body.appendChild(script);

        return () => {
            if (paypalRenderedRef.current) {
                console.log("Cleaning up PayPal buttons.");
                paypalRenderedRef.current = false;
            }

            if (document.body.contains(script)) {
                document.body.removeChild(script);
            }
        };
    }, [cart, paypalRef, createOrder, onApprove]);
};

export default usePaypalButtons;
